import {getToken,getSid} from '../http.interceptor.js';

//
const loadSetting = function(app = getApp()){
	var promise = new Promise((resolve, reject) => {
		requests({
			host: app.globalData.host,
			url: 'home/getSys',
			data: {},
			success: function (res) {
				uni.hideLoading();
				//console.log(">>>");
				//console.log(res);
				resolve(res)
			},fail: function (e) {
				uni.hideLoading();
				reject("");
			}
		})
	});
	return promise;
}

